  height: 100vh;
}

.font-normal {
  font-weight: 400;
}

.font-medium {
  font-weight: 500;
}

.font-bold {
  font-weight: 700;
}

.font-extrabold {
  font-weight: 800;
}

.text-primary {
  color: #2185B2;
}

.text-secondary {
  color: #D94428;
}

.text-neutral-500 {
  color: #6D6E71;
