}

.bg-black {
  background-color: #181918;
}

.reveal-image {
  position: relative;
  --scaleX: 1;
  --scaleFull: 1.2;
  overflow: hidden;
}
.reveal-image:before {
  background-color: #FFFFFF;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  content: "";
  transform: scaleX(var(--scaleX));
  transform-origin: left;
  transition: 0.5s all ease-out;
  z-index: 10;
}
.reveal-image img {
  transition: 0.5s all ease-out;
  transform: scale(var(--scaleFull));
  width: 100%;
  height: 100%;
  object-fit: cover;
}